import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: ()=>import('view/')
    },
    {
      path: '/test/ajax',
      name: 'ajax',
      component: ()=>import('view/test/ajax')
    }
  ]
})



